// Swiper Customization for Hero Slider
.hero-slider.swiper {

    // Pagination dots styling
    .swiper-pagination {
        bottom: 0px !important;
        left: 50% !important;
        transform: translateX(-50%) !important;
        background: $white;
        text-align: center;
        z-index: 10;
        padding: 0.6rem 1.5rem 0rem 1.5rem;
        width: auto;
        margin: 0 auto;
        -webkit-border-top-left-radius: 15px;
        -webkit-border-top-right-radius: 15px;
        -moz-border-radius-topleft: 15px;
        -moz-border-radius-topright: 15px;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
    }

    .swiper-pagination-bullet {
        width: 12px;
        height: 12px;
        background: $dark-grey;
        margin: 0 8px;
        transition: all 0.3s ease;
        cursor: pointer;

        &-active {
            transform: scale(1.2);
            background: $gold;
        }
    }

    .swiper-pagination-bullet:hover:not(.swiper-pagination-bullet-active) {
        background: lighten($dark-grey, 10%);
    }

    // Hide navigation arrows (if needed later)
    .swiper-button-next,
    .swiper-button-prev {
        display: none;
    }
}

