/* Specifications Section */
.specifications-section {
    background: $white;
    padding: 5rem 0;
    width: 100%;
}

.specifications-header {
    text-align: center;
    margin-bottom: 3rem;
}

.specifications-title {
    font-size: 3.5rem;
    color: $text-dark;
    font-family: $font-heading;
    margin-bottom: 1rem;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.specifications-subtitle {
    font-size: 1rem;
    color: $text-light;
    font-family: $font-sans;
    font-style: italic;
    letter-spacing: 5px;
}

.specifications-tabs-wrapper {
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.specifications-tabs {
    justify-content: center;
    gap: 0rem;
    flex-wrap: wrap;

    .nav-item {
        border: none;
        text-align: center;
    }

    .nav-link {
        color: $text-dark;
        border: none;
        border-bottom: 0px;
        padding: 1rem;
        transition: all 0.3s ease;
        font-weight: 400;
        font-size: 0.8rem;
        text-transform: uppercase;
        letter-spacing: 1px;
        position: relative;
        background: #f8f8f8;
        margin-right: 0.1rem;
        min-width: 200px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 0px !important;

        &:hover {
            color: $gold;
            background: $dark-grey-hover;
            border: 0px;
        }

        &.active {
            color: $gold;
            background: $dark-grey;
            border: 0px;
        }

        &.active::after {
            content: "";
            position: absolute;
            bottom: -7px;
            /* lekko zachodzi na border */
            left: 50%;
            transform: translateX(-50%);

            width: 0;
            height: 0;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-top: 10px solid $dark-grey;
            /* ← kolor taki sam jak tło contentu! */
        }
    }
}

.specifications-tab-content {
    margin-top: 3rem;

    .tab-pane {
        animation: fadeIn 0.4s ease-in-out;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.specifications-content-row {
    display: flex;
    align-items: flex-start;
    gap: 3rem;
    flex-wrap: nowrap;
