    }

    .overlay-title {
        font-size: 1.75rem;
        text-transform: uppercase;
    }

    .overlay-description {
        font-size: 0.9375rem;
    }

    /* News Section Responsive */
    .news-section {
        padding: 3rem 0;
    }

    .news-title {
        font-size: 2.5rem;
    }

    .news-subtitle {
        font-size: 0.875rem;
    }

    .news-card-content {
