    }

    .news-card-title {
        font-size: 1.5rem;
    }

    .news-features li {
        font-size: 0.875rem;
    }

    /* Top Bar Responsive */
    .top-bar {
        padding: 0;
    }

    // Bootstrap Grid handles layout - row and col-* classes automatically stack on mobile

    .top-bar-left {
        flex-wrap: wrap;
        gap: 0.5rem;
    }

    .top-bar-logo {
        font-size: 0.875rem;
    }

    .top-bar-right {
        flex-wrap: wrap;
        gap: 1rem;
        width: 100%;
    }

    .top-bar-item {
        font-size: 0.75rem;
    }

    .top-bar-social {
        margin-left: 0;
        padding-left: 0;
        border-left: none;
        border-top: 1px solid rgba(0, 0, 0, 0.1);
        padding-top: 0.75rem;
        width: 100%;
        justify-content: flex-start;
    }

    /* Navigation Responsive */
    .navbar {
        padding: 1rem 0;
    }

    .logo-image {
        max-height: 60px;
        max-width: 250px;
    }

    .phone-call {
        display: none;
    }

    .nav-menu {
        display: none !important; // Hidden by default on mobile
        position: fixed;
        left: -100%;
        top: 140px;
        flex-direction: column;
        background: var(--dark-grey);
        width: 100%;
        text-align: left;
        transition: left 0.3s ease;
        box-shadow: var(--shadow-lg);
        padding: 1rem 0;
        gap: 0;
        max-height: calc(100vh - 140px);
        overflow-y: auto;
        z-index: 999;

        &.active {
            display: flex !important; // Show as flex when active (hamburger clicked)
            left: 0;
        }
    }

    .nav-menu li {
        padding: 0;
        width: 100%;
    }

    .nav-link {
        padding: 1rem 2rem;
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    // Bootstrap handles dropdown menu on mobile automatically
    .dropdown-menu {
        width: 100%;
    }

    .dropdown-item {
        padding: 0.75rem 3rem;

        &:hover,
        &:focus {
            padding-left: 3.5rem;
        }
    }

    .hamburger {
        display: flex;
    }

    .hamburger.active span:nth-child(1) {
        transform: rotate(45deg) translate(8px, 8px);
    }

    .hamburger.active span:nth-child(2) {
        opacity: 0;
    }

    .hamburger.active span:nth-child(3) {
        transform: rotate(-45deg) translate(7px, -7px);
    }

    // Bootstrap Grid handles responsive layout automatically

    /* Map Section Responsive */
    .google-map {
        height: 300px;
    }

    /* Footer Responsive */
    .footer-content {
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
    }

    .footer-logo {
        max-height: 60px;
        max-width: 300px;
    }
