        &:hover {
            color: $gold-hover;
            text-decoration: underline;
        }
    }

    &.active {
        color: $text-light;
    }

    &+.breadcrumb-item::before {
        content: "/";
        color: $text-light;
        padding: 0 0.5rem;
    }
}

.news-detail-section {
    background: $white;
