        color: rgba(255, 255, 255, 0.8);
        text-decoration: none;
        font-size: 16px;
        transition: color 0.3s ease;
        padding: 10px 20px;

        &:hover {
            color: $gold;
            text-decoration: none;
        }

        i {
            margin-right: 8px;
            color: $gold;
        }

        @media (max-width: 768px) {
            font-size: 14px;
            padding: 8px 15px;
        }
    }
}

.scroll-top-btn {
    position: fixed;
    bottom: 30px;
    right: 30px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: var(--gold);
    color: var(--text-dark);
    border: none;
    font-size: 24px;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    z-index: 999;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}